@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", sans-serif;
  color: #fff;
  font-size: 1rem;
}

.mynavbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 2.5rem;
  position: absolute;
  z-index: 1;
  width: 100%;
  color: #fff;
}

.mynavbar-sticky {
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 1px 1px 1px #222;
  animation: moveDown 0.5s ease-in-out;
  color: #444;
  z-index: 50;
  opacity: 0.9;
}

.navbar--logo-holder {
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar--logo {
  width: 10rem;
  height: 10rem;
  margin-right: 0.5rem;
  animation: rotate 0.7s ease-in-out 0.5s;
}

.navbar--logo-sticky {
  width: 5rem;
  height: 5rem;
  margin-right: 0.5rem;
  animation: rotate 0.7s ease-in-out 0.5s;
}

.navbar--link {
  display: flex;
  list-style: none;
}
.navbar--link-item a {
  color: #fff;
  text-decoration: none;
}
.navbar--link-item a::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: #fff;
  transition: width 0.3s;
}
.navbar--link-item a:hover {
  color: lightgrey;
  text-decoration: none;
}
.navbar--link-item a:hover::after {
  width: 100%;
  /* transition: width .3s; */
}

.navbar--link-item-sticky a {
  color: #000;
}
.navbar--link-item-sticky a:hover {
  color: lightgrey;
  text-decoration: none;
}
.navbar--link-item {
  margin: 0.4rem 1rem 0 0;
  padding: 0 0.3rem;
  cursor: pointer;
  font-size: 1.2rem;
  text-transform: capitalize;
}

.navbar--link-item-sticky {
  margin: 0.4rem 1rem 0 0;
  padding: 0 0.3rem;
  cursor: pointer;
  font-size: 1rem;
  text-transform: capitalize;
}

@keyframes moveDown {
  from {
    transform: translateY(-5rem);
  }
  to {
    transform: translateY(0rem);
  }
}

@keyframes rotate {
  0% {
    transform: rotateY(360deg);
  }
  100% {
    transform: rotateY(0rem);
  }
}

.about {
  padding: 2rem;
  width: 70rem;
  color: #444;
  text-align: justify;
  margin: auto;
  text-align: center;
  padding-top: 12%;
  padding-bottom: 10%;
}

.footer {
  padding: 1rem;
  padding-left: 2rem;
  width: 100%;
  color: #fff;
  text-align: left;
  margin: auto;
  background-color: #000;
}

.scrollTop {
  position: fixed;
  width: 100px;
  bottom: 20px;
  right: 0px;
  align-items: center;
  height: 20px;
  justify-content: center;
  z-index: 1000;
  cursor: pointer;
  animation: fadeInArrow 0.3s;
  transition: opacity 0.4s;
  opacity: 0.5;
  color: grey;
}

.scrollTop:hover {
  opacity: 1;
}

@keyframes fadeInArrow {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}

.portfolio {
  padding: 2rem;
  width: 100%;
  color: #fff;
  text-align: center;
  margin: auto;
  background-color: #000;
  text-align: center;
  padding-bottom: 8%;
  padding-top: 10%;
}
.carousel-description {
  position: absolute;
  left: 50%;
  bottom: 0%;
  transform: translate(-50%, 0);
  text-align: center;
  background-color: #000000d9;
  padding: 25px;
  width: 100%;
}
.carousel-description a {
  color: #fff;
  text-decoration: none;
}
.carousel-description a:hover {
  color: lightgrey;
  text-decoration: none;
}

.skills {
  padding: 2rem;
  width: 100%;
  color: #444;
  text-align: center;
  margin: auto;
  padding-top: 5%;
  padding-bottom: 5%;
  animation-name: backgroundColorPalette;
  animation-duration: 40s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: linear;
}
.uniqueCounters {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.counterColumn {
  width: 30%;
}

@keyframes backgroundColorPalette {
  0% {
    background: #fff;
  }
  25% {
    background: #abb0b5;
  }
  50% {
    background: #fff;
  }
  75% {
    background: #abb0b5;
  }
  100% {
    background: #fff;
  }
}

.education {
  padding: 2rem;
  width: 100%;
  color: #444;
  margin: auto;
  text-align: center;
  padding-bottom: 10%;
  padding-top: 5%;
  background-color: #abb0b5;
}
.carousel-description {
  position: absolute;
  left: 50%;
  bottom: 0%;
  transform: translate(-50%, 0);
  text-align: center;
  background-color: #000000a1;
  padding: 25px;
  width: 100%;
  color: #fff;
}
.carousel-description a {
  color: #fff;
  text-decoration: none;
}
.carousel-description a:hover {
  color: lightgrey;
  text-decoration: none;
}

.testimonials {
  padding: 2rem;
  width: 70rem;
  color: #444;
  text-align: justify;
  margin: auto;
  padding-top: 5%;
  padding-bottom: 7%;
  text-align: center;
}
.uniqueTestimonials {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.testimonialsColumn {
  width: 30%;
}
.star {
  width: 40px;
  align-items: center;
  height: 20px;
  justify-content: center;
  color: grey;
}

.Container {
  position: relative;
  min-height: 300px;
  max-height: 800px;
  overflow: hidden;
}
.Video {
  width: 100%;
  height: 100%;
}
.Content {
  position: absolute;
  top: 0;
  color: #f1f1f1;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}
.SubContent {
  text-align: center;
  padding: 10px;
}
.SubContent p {
  color: #eeeeeead;
  font-size: 1.3rem;
}
.SubContent button {
  font-weight: bold;
  margin-top: 30px;
  color: #f1f1f1;
  border: #f1f1f1 1px solid;
  padding: 10px 5px;
  background-color: hsla(0, 0%, 0%, 0);
  font-size: 1.25rem;
  border-radius: 5px;
  transition: background-color 0.5s;
}
.SubContent button:hover {
  background-color: #f1f1f1;
  color: #000000c0;
}
.SubContent img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  display: block;
  margin: 15px auto;
  border: #f1f1f1 1px solid;
}
.banner-content {
  position: absolute;
  left: 50%;
  top: 30%;
  transform: translate(-50%, 0);
  text-align: center;
  color: #fff;
}
.large {
  font-size: 8rem;
}

.anim-text {
  text-align: center;
}

.contact {
  padding: 2rem;
  width: 100%;
  color: #fff;
  text-align: center;
  margin: auto;
  background-color: #000;
  padding-top: 5%;
  padding-bottom: 5%;
}
.uniqueContact {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 5%;
}
.contactColumn {
  width: 48%;
}
.contactColumn button {
  width: 100%;
  padding: 14px;
  transition: background-color 0.5s;
}
.gitLinks {
  padding-top: 7%;
}
.socialLink {
  width: 80px;
  margin: 4px;
  transition: width 0.2s;
}
.socialLink:hover {
  width: 75px;
  opacity: 0.7;
}

